.team {
    padding-top: 100px;
    &__title {
        margin-bottom: 30px;
    }
    &__wrap {
        display: flex;
    }
    &__info {
        margin: 0 15px;
        max-width: 300px;
        width: 100%;
    }
    &__list {
        display: flex;
        flex-wrap: wrap;
        
    }
    &__item {
        width: calc(50% - 30px);
        margin: 0 15px 30px;
    }
    &__text {
        color: #243f4d;
        font-family: "Rubik", sans-serif;
        margin-bottom: 60px;
        line-height: 1.4;
    }
    @media screen and (max-width: 991px) {
        padding: 50px 0;
        &__wrap {
            flex-wrap: wrap;
        }
        &__info {
            max-width: 100%;
            margin-bottom: 25px;
        }
        
    }
    @media screen and (max-width: 767px) {
        padding: 30px 0;
        &__list {
            justify-content: center;
        }
        &__item {
            max-width: 315px;
            width: 100%;
        }
    }
    @media screen and (max-width: 575px) {
        &__list {
            width: 100%;
        }
        &__item {
            margin-left: 0;
            margin-right: 0;
        }
        &__text {
            margin-bottom: 25px;
        }
    }
}

.worker {
    &__img {
        height: 100%;
        object-fit: cover;
        &-wrap {
            height: 320px;
            overflow: hidden;
        }
    }
    &__info {
        // width: calc(100% - 45px);
        margin-right: 45px;
        background-color: #fff;
        margin-top: -50px;
        position: relative;
        z-index: 1;
        display: flex;
        justify-content: space-between;
        padding: 30px 20px;
        box-shadow: 0 0 30px #e8e8e8;
    }
    &__name {
        color: #436372;
        font-weight: 700;
        font-size: 22px;
        font-family: "DM Sans", sans-serif;
        margin-bottom: 20px;
    }
    &__post {
        font-family: "Rubik", sans-serif;
        color: #565872;
    }
    &-social {
        display: flex;
        &__item {
            &:not(:last-child) {
                margin-right: 10px;
            }
        }
        &__link {
            width: 40px;
            height: 40px;
            display: flex;
            flex-shrink: 0;
            justify-content: center;
            align-items: center;
            background-color: #f6f6f6;
            border-radius: 50%;
        }
    }


}